<template>
<div class="bottom-toast" v-show="isShow">
  <div class="phone">{{phoneNumber}}</div>
  <div class="call">呼叫</div>
  <div class="cancel" @click="cancel">取消</div>
</div>
</template>

<script>
export default {
  name: "BottomToast",
  props:{
    isShow:{
      type:Boolean,
      default:false,
    },
    phoneNumber:{
      type:String,
      default: '',
    }
  },
  methods:{
    cancel(){
      this.$emit('cancel')
    }
  }
}
</script>

<style scoped>
.bottom-toast{
  width: 100%;
  background-color: #ffffff;
  position: fixed;
  bottom: 0;
  z-index: 10000;
  /*border-radius: 15px;*/
  text-align: center;
}
.phone{
  border-bottom: 1px solid #eeeeee;
  height: 40px;
  line-height: 40px;
}
.call,.cancel{
  font-size: 18px;
  color: black;
  height: 50px;
  line-height: 50px;
  border-bottom: 6px solid #eeeeee;
}
</style>