<template>
<div class="login-toast" v-show="isShow">
  <div @click="closeClick" class="close">
    <img src="~assets/img/profile/close.svg">
  </div>
  <div class="login-text">登录</div>
  <div class="form">
    <label>用户名<input name="username" type="name" v-model="username"></label>
    <label>密码 <input name="password" type="password" v-model="password"></label>
    <div class="button"><button @click="btnClick">登录</button></div>
  </div>
</div>
</template>

<script>
export default {
  name: "LoginToast",
  props:{
    isShow:{
      type:Boolean,
      default:false,
    }
  },
  data(){
    return{
      username:'',
      password:'',
    }
  },
  methods:{
    closeClick(){
     this.$emit('closeClick')
    },
    btnClick(){
      this.$emit('btnClick',this.username,this.password)
    },
  }
}
</script>

<style scoped>
.login-toast{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  z-index: 999;
  background:rgba(0,0,0,0.2);
  color: #fff;
}
.login-text{
  font-size: 20px;
  text-align: center;
  background: linear-gradient(135deg,#56c8ff,#6f99fc) no-repeat;
}
.login-toast .form{
  margin: 20px;
  font-size: 16px;
}
.login-toast .form input{
  margin-bottom: 10px;
  /*除去input框默认样式*/
  /*去除阴影*/
  box-shadow:none;
  /*聚焦input的蓝色边框*/
  outline: none;
  /*textarea 禁止拖拽*/
  resize: none;
  /*去除边框*/
  border: none;
  /*常用于IOS下移除原生样式*/
  -webkit-appearance: none;
  /*点击高亮的颜色*/
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}
.button{
  text-align: center;
}
.close{
  position: fixed;
  right: 0;
}
.close img{
  width: 20px;
  height: 20px;
}
</style>