<template>
<div id="cart">
  <div class="image" :class="{active:isActive}">
    <img src="~assets/img/common/cart.svg" alt="" @click="cartClick">
  </div>
  <div class="detail-count" v-show="isActive">
    <Count :count="totalCount"></Count>
  </div>
  <div class="message">
  <span>{{totalPrice}}</span>
    <div v-show="isActive" class="fee">另需配送费￥ {{item.shippingFee}}</div>
  </div>
  <div class="price" :class="{success:isActive}" >
    <span v-if="!isActive">￥{{item.price}}起送</span>
    <span @click="pay" v-else>去结算</span>
  </div>
</div>
</template>

<script>
import Count from "../../common/count/Count";
export default {
  name: "Cart",
  components: {Count},
  data(){
    return{
      nowTime:null,
      busPhone:'',
      rider:{ },
    }
  },
  props:{
    item:{
      type:Object,
      default(){
        return {}
      }
    },
    isActive:{
      type:Boolean,
      default:false,
    },
    totalPrice:{
      type:String,
      default:'购物车空空如也',
    },
    totalCount:{
      type:Number,
      default:0,
    }
  },
  computed:{
    user(){
      return this.$store.state.user;
    }
  },
  methods:{
    cartClick(){
      this.$emit('cartClick')
    },
    pay(){
      if(this.totalCount!=0&&this.user.phone!=null){
        this.$toast.show('结算成功')
        console.log(this.user)
        this.nowTimes();
        this.genBusPhone();
        this.genRider();
        this.$store.commit('getOrderList',{
          rider:this.rider,
          busPhone:this.busPhone,
          nowTime:this.nowTime,
          totalCount:this.totalCount,
          totalPrice:this.totalPrice,
          name:this.item.name,
          image:this.item.image,
          price:this.item.price,
          list:this.item.detail.cartList,
        })
        this.$emit('pay')
      }
      else if(this.totalCount==0){
        this.$toast.show('你还未添加任何商品')
      }
      else if(this.user.phone==null){
        this.$toast.show('你还未登录，请前往登录')
        this.$router.push('/login')
        // setTimeout(function (){
        //   this.$router.push('/login')
        // },2000)

      }

    },
      // 获取当前时间
    nowTimes(){
      let y=new Date().getFullYear();
      let M=new Date().getMonth()+1;
      let d=new Date().getDate();
      let h=new Date().getHours();
      let m = new Date().getMinutes()<10?'0'+new Date().getMinutes():new Date().getMinutes();
      let s=new Date().getSeconds();
      let date=y+'-'+M+'-'+d+' '+h+':'+m+':'+s
      this.nowTime=date;
      console.log(this.nowTime)
    },
    //随机生成商家电话
    genBusPhone(){
      let arr=new Array(11);
      arr[0]=1;
     for(let i=1;i<arr.length;i++){
       let num=Math.round(Math.random()*9)
        arr[i]=num
     }
     this.busPhone=arr.slice(0,3).join('')+'-'+arr.slice(3,7).join('')+"-"+arr.slice(7,arr.length).join('')
    },
    //随机生成骑手电话
    genRiderPhone(){
      let arr=new Array(11);
      arr[0]=1;
      for(let i=1;i<arr.length;i++){
        let num=Math.round(Math.random()*9)
        arr[i]=num
      }
      return arr.slice(0,3).join('')+'-'+arr.slice(3,7).join('')+"-"+arr.slice(7,arr.length).join('')
    },
    //随机分配一个骑手
    genRider(){
      let rider={
        riderName:'配送员',
        riderPhone: this.genRiderPhone(),
      }
      this.rider=rider;
      console.log(this.rider)
    },
  }

}
</script>

<style scoped>
#cart{
  height: 49px;
  position: fixed;
  bottom: 0px;
  width: 100%;
  z-index: 999;
  display: flex;
  font-size: 14px;
  background-color: #ffffff;
  line-height: 49px;
}
.message{
  flex: 1;
  padding-left: 20px;
  border-right: 2px solid #f6f6f6;
  height: 49px;
}
.image img{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: relative;
  bottom: 20px;
  left: 15px;
  background-color:#444444;
  margin-right: 10px;
  padding: 5px;
  z-index: 1;
}
.price{
  width: 100px;
  position: relative;
 text-align: center;
}
.message span{
  font-size: 18px;
}
.active img{
  background-color: #00BFFF;
}
.detail-count{
  z-index: 999;
  position: relative;
  bottom: 20px;
  right: 5px;
}
.success{
  background-color:	#00BFFF;
  color:#ffffff;
}
.fee{
  font-size: 10px;
  color: #444444;
  position: relative;
  bottom: 35px;
}
</style>