<template>
  <div class="select">
    <ul>
      <li v-for="(item,index) in labelList"
          @click="getValue(index,item)"
          :class="{active:currentIndex===index}"
      >
        <img v-if="currentIndex!=index" :src="item.image1" alt="">
        <img v-else :src="item.image2" alt="">
        {{item.value}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "SelectViewItem",
  data(){
    return{
      currentIndex:0,
    }
  },
  props:{
    labelList: {
      type:Array,
      default(){
        return []
      }
    },
  },
  methods:{
    getValue(index){
      this.currentIndex=index;
      this.$emit('getValue',index)
    }
  }
}
</script>

<style scoped>
.select{
  /*border-top: 1px solid #cccccc;*/
  overflow: hidden;
}
.select ul li{
  /*width: 100%;*/
  height: 40px;
  cursor: pointer;
  line-height: 40px;
  padding-left: 10px;
  border-bottom: 2px solid #eeeeee;
}
.active{
  background-color: #eeeeee;
  color: #ff8198;
}
.select img{
  width: 15px;
  height: 15px;
}
