<template>
<div id="detail">
  <!--导航  -->
  <search-nav-bar
      class="detail-nav"
      :title="$route.params.name"
      @backClick="backClick">
  </search-nav-bar>
  <detail-nav-bar :item="item"></detail-nav-bar>
  <detail-tab-control></detail-tab-control>
  <div class="detailList">
    <!-- 侧边按钮   -->
    <main-slide
        :detail="item.detail"
        @slideItemClick="slideItemClick"
    ></main-slide>
    <scroll
        class="content"
        :probe-type="3"
        :pull-up-load="true"
        ref="scroll"
    >
    <!-- 店铺食品详情     -->
        <detail-list :detail="item.detail"  @addCart="addCart"></detail-list>
    </scroll>
  </div>
    <!--添加购物车    -->
   <cart  :item="item"
          :total-count="totalCount"
          :is-active="item.isActive"
          :total-price="totalPrice"
          @pay="pay"
          @cartClick="cartClick"></cart>
    <scroll
        v-if="item.isShow"
        class="content1"
        :probe-type="3"
        :pull-up-load="true"
        ref="scroll2"
        :style="{backgroundColor:activeColor}"
    >
    <!-- 购物车列表     -->
      <cart-view
          :cart-list="item.detail.cartList"
          v-if="item.isShow"
          @increase="increase"
          @reduce="reduce"
          @deleteCart="deleteCart"
      >
      </cart-view>
    </scroll>
</div>
</template>
<script>
import NavBar from "../../components/common/navbar/NavBar";
import DetailNavBar from "./childComps/DetailNavBar";
import DetailTabControl from "./childComps/DetailTabControl";
import MainSlide from "./childComps/MainSlide";
import DetailList from "./childComps/DetailList";
import Scroll from "../../components/common/scroll/Scroll";
import {imgLoadMixin} from "../../common/mixin";
import Cart from "../../components/content/cart/Cart";
import SearchNavBar from "../../components/content/search/SearchNavBar";
import CartView from "../../components/content/cartView/CartView";
export default {
  name: "Detail",
  components: {Cart, DetailList, MainSlide, DetailTabControl, DetailNavBar, NavBar,Scroll,SearchNavBar,CartView},
  data(){
    return {
      itemTopsY:[],
      // slideCount:0,
      slideIndex:0,
    }
  },
  mounted() {
    //监听图片加载，在图片加载完成刷新滚动高度
    this.$nextTick(()=>{
      this.$bus.$on('imgLoad',()=>{
        this.$refs.scroll.refresh();
      })
    })
  },
  computed:{
    item(){
      return this.$store.state.item;
    },
    //计算购物车中商品总价格
    totalPrice(){
      return this.item.detail.cartList.reduce((preValue,item)=>{
       return  preValue+item.count*item.price
      },0).toFixed(2)
    },
    activeColor(){
      return this.item.isShow==true?'#ffffff':'';
    },
    //计算购物车中商品数量
    totalCount(){
        return this.item.detail.cartList.reduce((preValue,item)=>{
          return preValue+item.count;
        },0)
      },
    active(){
      return this.totalCount!=0?true:false;
    }
  },
  methods:{
    //联动效果
    slideItemClick(index){
      this.$refs.scroll.scrollTo(0,-(this.item.detail.topsY[index]))
    },
    //加入购物车
    addCart(item1,index1,cartItem){
      this.slideIndex=index1;
      this.item.isShow=true;
      this.item.isActive=true;
      this.item.detail.slideList[index1].isSlideShow=true;
      this.item.detail.slideList[index1].slideCount++;
      let oldItem=this.item.detail.cartList.find((item)=>item.id==cartItem.id);
      if(oldItem){
        cartItem.count+=1;
      }
      else{
        this.item.detail.cartList.push(cartItem)
      }
    },
    cartClick(){
      this.item.isShow=!this.item.isShow;
    },
    //添加商品数量
    increase(item){
      this.item.detail.slideList[this.slideIndex].slideCount++;
      item.count++;
    },
    //减少商品数量
    reduce(item) {
      this.item.detail.slideList[this.slideIndex].slideCount--;
      if(this.item.detail.slideList[this.slideIndex].slideCount<1){
        this.item.detail.slideList[this.slideIndex].isSlideShow=false;
      }
      if(item.count>1){
        item.count--;
      }
      else{
        this.item.detail.cartList.splice(this.item.detail.cartList.indexOf(item),1)
      }
    },
    backClick(){
      this.$router.push('/home')
      this.item.totalCount=this.totalCount;
      this.item.isActive=this.active;
      this.$refs.scroll.scrollTo(0,0,0)
    },
    //清空购物车
    deleteCart(){
      console.log('delete');
      this.item.isShow=false;
      this.item.detail.cartList=[];
      this.item.isActive=false;
      this.item.detail.slideList.forEach((item)=>{
        item.slideCount=0;
        item.isSlideShow=false
      })
    },
    pay(){
      this.deleteCart()
    }
  },
}
</script>

<style scoped>
#detail{
  height: 100vh;
  position: relative;
}
.detail-nav{
  background-color: black;
  color: #ffffff;
}
.detailList{
  display: flex;
}
.content{
  flex: 1;
  height: 450px;
  overflow:hidden ;
}
.content1{
  width: 100%;
  overflow: hidden;
  position: fixed;
  bottom: 49px;
  height: 200px;
  /*background-color: #ffffff;*/
}
</style>