          case 'recent':
            moreItem=this.recentMore.find((item) => item.page == page);
            break;
          case 'sell':
            moreItem=this.sellMore.find((item) => item.page == page);
        }
        if(moreItem){
          this.foods[type].list.push(...moreItem.list);
          this.$store.commit('getFoods',moreItem.list)
        }
    },
    //事件监听方法
    // 点击进入搜索商品页面
    inputClick(){
      this.$router.push('/search')
    },
    //点击进入定位地址页面
    addressClick(){
      this.$router.push('/address')
    },
    //点击滑动到顶端
    topClick(){
      this.$refs.scroll.scrollTo(0,0)
    },
    //实时监听滚动的位置决定搜索框、置顶按钮什么时候出现
    homeScroll(position){
      this.isShowTop=(-position.y)>500;
      this.isShowInput=(-position.y)>400;
    },
    //获取不同的商品类别
    getValue(index){
      this.label=this.labelList[index].value;
      //延迟500ms
      setTimeout(()=>{
         this.isShow=false
        },500);
      this.$refs.scroll.scrollTo(0,0)
      switch (index){
        case 0:
          this.currentType='recent';
          break;
        case 1:
          this.currentType='sell';
          break;
        case 2:
          this.currentType='score';
          this.$toast.show('敬请期待更多商品')
          break;
        case 3:
          this.currentType='price';
          this.$toast.show('敬请期待更多商品')
          break;
        case 4:
          this.currentType='speed';
          this.$toast.show('敬请期待更多商品')
          break;
      }
    },
    openValue(labelList) {
      this.labelList=labelList;
      this.isShow=true;
      this.isTop=true;
    },
    //下拉加载更多页商品
    pullingUp(){
      switch (this.currentType){
        case 'recent':
          this.getMoreFoods('recent');
          break;
        case 'sell':
          this.getMoreFoods('sell');
          break;
      }
      this.$refs.scroll.finishPullUp()
     }
  },
  //混入
  mixins:[imgLoadMixin],
  created() {
    console.log('home created');
    this.$store.commit('getFoods',this.foods['recent'].list)
    this.$store.commit('getFoods',this.foods['sell'].list)
  },
  //记录不活跃离开的时候滚动的高度
  deactivated() {
    this.scrollY=this.$refs.scroll.scrollY()
  },
  //处于活跃状态则滚动到上次浏览的高度
  activated() {
    this.$refs.scroll.refresh();
    this.$refs.scroll.scrollTo(0,this.scrollY,0)
  },
}
</script>

<style scoped>
#home{
  height: 100vh;
  position: relative;
}
.content{
  height: calc(100% - 44px  - 49px );
  overflow: hidden;
}
.home-nav{
  background: linear-gradient(135deg,#56c8ff,#6f99fc);
  color: #fff;
}
.input-box{
  width: 100%;
  position:sticky;
  top:44px;
  z-index:2;
}
.top{
  position: fixed;
  top:44px;
  z-index: 3;
  background-color: #ffffff;
}
.input-box input{
  width: 100%;
  height: 36px;
  line-height: 36px;
  text-align: center;
  /*去除阴影*/
  box-shadow: 0px 0px 10px rgba(100,100,100,0.1);
  /*聚焦input的蓝色边框*/
  outline: none;
  /*textarea 禁止拖拽*/
  resize: none;
  /*去除边框*/
  border: none;
  /*常用于IOS下移除原生样式*/
  -webkit-appearance: none;
  /*点击高亮的颜色*/
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}
</style>