<template>
<div class="home-search">
  <div class="address" @click="addressClick">
    <img src="~assets/img/select/position.svg" alt="">
    <span>{{address}}</span>
    <img src="~assets/img/common/arrow.svg" alt="">
  </div>
  <div  @click="inputClick">
    <input-box></input-box>
  </div>
</div>
</template>

<script>
import InputBox from "../../../components/common/input/InputBox";
export default {
  name: "HomeSearch",
  components: {InputBox},
  data(){
    return{
      address:'点击定位',
    }
  },
  methods:{
    addressClick(){
      this.$emit('addressClick')
    },
    inputClick(){
      this.$router.push('/search')
    }
  },
  mounted() {
    this.$bus.$on('mapSearchInit',(res)=>{
    this.address=res;
  })
    this.$bus.$on('posClick',(res)=>{
      this.address=res;
    })

  }
}
</script>

<style scoped>
.home-search{
  background-color: #ffffff;
  width: 100%;
  display: flex;
  height: 40px;
  line-height: 40px;
  border: 1px solid #eeeeee;
  font-size: 14px;
}
.address{
  width: 160px;
  overflow: hidden;
}
.address img{
  width: 18px;
  height: 18px;
  position: relative;
  top:4px;
  margin-left: 10px;
}

</style>