<template>
  <div>
    <slide-nav-bar>
      <div class="search">
        <img src="~assets/img/common/search.svg" alt="">
      </div>
      <slide-item
          v-for="(item,index) in detail.slideList"
          :key="index"
          @click.native="slideItemClick(index)"
          :class="{active:currentIndex===index}"
          class="slide-item"
      >
        <div>{{item.name}}</div>
        <div class="count" v-show="item.isSlideShow">
          <Count :count="item.slideCount"></Count>
        </div>
      </slide-item>
    </slide-nav-bar>
  </div>
</template>

<script>

import SlideNavBar from "../../../components/common/slideNavBar/SlideNavBar";
import SlideItem from "../../../components/common/slideNavBar/SlideItem";
import Count from "../../../components/common/count/Count";
export default {
  name: "MainSlide",
  components: {Count, SlideItem, SlideNavBar},
  props:{
    detail: {
      type:Object,
      default(){
        return {}
      }
    },
  },
  data(){
    return{
      currentIndex:0,
    }
  },
  activated(){
   this.currentIndex=0;
  },
  methods:{
    slideItemClick(index){
      this.currentIndex=index;
      this.$emit('slideItemClick',index)
    }
  },
}
</script>

<style scoped>
.active{
  color: #000;
  background-color: #ffffff;
}
.search{
  border-bottom: 1px solid #eeeeee;
  border-right: 1px solid #eeeeee;
  background-color: #f6f6f6;
}
.search img{
  width: 25px;
  height: 25px;
}
.slide-item{
  display: flex;
}
.count{
  position: relative;
  z-index: 999;
  bottom:5px;
  left: 5px;
}
</style>