<template>
<div>
  <div class="login-wrapper">
    <div id="login">
      <p class="title">登录</p>
      <el-form
          :model="loginForm"
          status-icon
          :rules="rules2"
          ref="loginForm"
          label-width="0"
      >
        <el-form-item prop="phone">
          <el-input  v-model="loginForm.phone" auto-complete="off" placeholder="请输入手机号"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" type="password" auto-complete="off" placeholder="输入密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"  style="width:100%;" @click="submitForm('loginForm')">登录</el-button>
          <p class="login" @click="gotoRegister">没有账号？立即注册</p>
        </el-form-item>
      </el-form>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: "Login",
  data(){
    let checkTel = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号码'))
      } else if (!this.checkMobile(value)) {
        callback(new Error('手机号码不正确'))
      } else {
        callback()
      }
    }
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"))
      } else  if(!this.checkPass(value)){
        callback(new Error('密码不正确'))
      }else{
        callback()
      }
      }
    return{
      loginForm: {
        username:this.$route.query.username,
        phone:'',
        password: '',
      },
      rules2: {
        phone: [{ validator: checkTel, trigger: 'change' }],
        password: [{ validator: validatePass, trigger: 'change' }],
      },
      // user:{
      //   phone:'',
      //   password:''
      // }
    }
  },
  methods:{
    //验证手机号
    checkMobile(str) {
      if(str!=this.$route.query.phone){
        return false;
      }
      else{
        return true;
      }
    },
    //验证密码
    checkPass(str){
      if(str!=this.$route.query.password){
        return false;
      }
      else{
        return true;
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          setTimeout(() => {
            alert('登录成功')
            this.$router.push('/profile')
            this.$store.commit('getUser',this.loginForm)
          }, 400);
        } else {
          return false;
        }
      })
    },
    gotoRegister(){
      this.$router.push('/register')
    }
  }
}
</script>

<style scoped>
.login-wrapper{
  background: url("../../../assets/img/common/bg1.jpeg");
  position: fixed;
  bottom: 0px;
  width: 100%;
  z-index: 999;
  height: 100%;
}
.login-wrapper img {
  position: absolute;
  z-index: 1;
}
.login-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
#login {
  width: 340px;
  margin: 60px auto;
  /*background: #fff;*/
  padding: 20px 40px;
  /*border-radius: 10px;*/
  /*opacity: 0.5;*/
  position: relative;
  z-index: 9;
}
.title {
  font-size: 26px;
  line-height: 50px;
  font-weight: bold;
  margin: 10px;
  text-align: center;
}
.el-form-item {
  text-align: center;
}
.el-button--primary:focus {
  background: #409EFF;
  border-color: #409EFF;
  color: #fff;
}
.login {
  margin-top: 10px;
  font-size: 14px;
  line-height: 22px;
  color: #1ab2ff;
  cursor: pointer;
  text-align: left;
  text-indent: 8px;
  width: 160px;
}
.login:hover {
  color: #2c2fd6;
}
</style>