<template>
  <div class="wrapper" ref="wrapper">
    <div class="content" ref="content">
      <div class="order-item">
        <div v-for="item in orderItem.list" class="list">
          <img :src="item.image" alt="" @load="imgLoad">
          <div class="item-name">{{item.name}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";

export default {
  name: "OrderScroll",
  data() {
    return {
      scroll:null,
    };
  },
  methods: {
    verScroll () {
      //动态计算出滚动区域的大小，产生滚动的原因是滚动区域宽度大于父盒子宽度
        let width = this.orderItem.list.length * 120
        //修改滚动区域的宽度
        this.$refs.content.style.width = width + 'px'
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            startX: 0,
            click: true,
            scrollX: true,
            scrollY: false,
            eventPassthrough: 'vertical'
          })
        } else {
          //如果dom结构发生改变调用该方法
          this.scroll.refresh()
        }
      })
    },
    imgLoad(){
      this.$bus.$emit('imgLoad')
    }
  },
  mounted () {
    this.$nextTick(() => {
      let timer = setTimeout(() => {
        if (timer) {
          clearTimeout(timer)
          this.verScroll()
        }
      }, 0)
    })
  },
  props:{
    orderItem: {
      type:Object,
      default(){
        return {}
      }
    }
  },
}
</script>

<style scoped>
.wrapper{
  width: 100%;
  height: 100px;
  overflow: hidden;
}
.order-item{
  display: flex;
}
.list{
  text-align: center;
  width: 100px;
  height: 80px;
  margin: 10px;
  font-size: 16px;
}
.list img{
  width: 60px;
  height: 40px;
}
.item-name{
  width: 100px;
  height: 20px;
  overflow: hidden;
  margin-top: 5px;
}
</style>