<template>
<div class="order-detail">
  <search-nav-bar
      class="order-detail-navbar"
      title="订单详情"
      @backClick="backClick">
  </search-nav-bar>
  <scroll
      :pull-up-load="true"
      :probe-type="3"
      class="content"
      ref="scroll"
  >
    <div class="detail">
      <order-detail-title></order-detail-title>
      <!--  骑手信息    -->
      <rider-info :rider="orderItem.rider"></rider-info>
      <Phone>
        <span slot="text" @click="callRider">致电骑手</span>
      </Phone>
      <div>
        <order-title :order-item="orderItem" @rightClick="rightClick" class="order-item">
          <div slot="right" class="business-phone">
            <img src="~assets/img/common/phone.svg" alt="">
          </div>
        </order-title>
        <!--    订单详情    -->
        <order-detail-list :order-item="orderItem"></order-detail-list>
        <Phone>
          <span slot="text" @click="callBusiness">致电商家</span>
        </Phone>
      </div>
      <!--   详情信息   -->
      <shipp-info :now-time="orderItem.nowTime"></shipp-info>
    </div>
  </scroll>
  <BottomToast :is-show="isShow" :phone-number="phoneNumber" @cancel="cancel"></BottomToast>
</div>
</template>

<script>
import SearchNavBar from "../../components/content/search/SearchNavBar";
import OrderTitle from "./childComps/OrderTitle";
import Phone from "./childComps/Phone";
import Scroll from "../../components/common/scroll/Scroll";
import OrderDetailList from "./childComps/OrderDetailList";
import OrderDetailTitle from "./childComps/OrderDetailTitle";
import RiderInfo from "./childComps/RiderInfo";
import ShippInfo from "./childComps/ShippInfo";
import BottomToast from "../../components/common/toast/BottomToast";
export default {
  name: "OrderDetail",
  data(){
    return {
      isShow:false,
      phoneNumber:''
    }
  },
  components:{
    BottomToast,
    ShippInfo,
    RiderInfo,
    OrderDetailTitle,
    OrderDetailList,
    Scroll,
    Phone,
    SearchNavBar,
    OrderTitle,
  },
  computed:{
    orderItem(){
      return this.$store.state.orderItem
    },
  },
  methods:{
    backClick(){
      this.$router.push('/order')
    },
    rightClick(){
      this.$router.push('/detail/'+this.orderItem.name)
    },
    //呼叫骑手
    callRider(){
      this.isShow=true
      this.phoneNumber=this.orderItem.rider.riderPhone
    },
    //呼叫商家
    callBusiness(){
      this.isShow=true
      this.phoneNumber=this.orderItem.busPhone
    },
    cancel(){
      this.isShow=false
    }
  }
}
</script>

<style scoped>
.order-detail{
  width: 100%;
  height: 100vh;
 background-color: #eeeeee;
}
.content{
  height: calc(100% - 44px -  49px);
  overflow: hidden;
}
.detail{
  margin-left: 10px;
  margin-right: 10px;
  background-color: #ffffff;
}
.order-item{
  height: 46px;
}
.business-phone{
  position: fixed;
  right: 20px;
}
.business-phone img{
  width: 30px;
  height: 30px;
}

</style>