import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "../views/home/Home";
import Order from "../views/order/Order";
import Profile from "../views/profile/Profile";
import Detail from "../views/detail/Detail";
import SearchView from "../components/content/search/SearchView";
import CommoditySearch from "../views/commodity/CommoditySearch";
import Result from "../views/commodity/Result";
import OrderDetail from "../views/orderDeatil/OrderDetail";
import UserInfo from "../views/userInfo/UserInfo";
import DetailAddress from "../views/detailAddress/DetailAddress";
import IncreaseAddress from "../views/detailAddress/childComps/IncreaseAddress";
import Register from "../views/profile/childComps/Register";
import Login from "../views/profile/childComps/Login";

Vue.use(VueRouter)
//路由导航冗余报错（路由重复）
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const routes = [
  {
    path:'',
    redirect:'/home',
  },
  {
    path: '/home',
    component:Home,
  },
  {
    path:'/order',
    component: Order,
  },
  {
    path:'/profile',
    component: Profile,
  },
  {
    path:'/detail/:name',
    component: Detail,
  },
  {
    path:'/address',
    component: SearchView,
  },
  {
    path:'/search',
    component: CommoditySearch,
  },
  {
    path: '/result/:name',
    component: Result,
  },
  {
    path: '/orderDetail/:name',
    component: OrderDetail,
  },
  {
    path:'/userInfo',
    component: UserInfo,
  },
  {
    path:'/detailAddress',
    component: DetailAddress,
  },
  {
    path:'/increaseAddress',
    component: IncreaseAddress
  },
  {
    path: '/register',
    component: Register
  },
  {
    path:'/login',
    component: Login
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
