<template>
<div :message="message" class="toast" v-show="isShow">
  {{message}}
</div>
</template>

<script>
export default {
  name: "Toast",
  data(){
    return{
      message:'',
      isShow:false,
    }
  },
  methods:{
    show(message,duration=2000){
      this.isShow=true;
      this.message=message;
      setTimeout(()=>{
        this.isShow=false;
        this.message='';
      },duration)
    }
  }

}
</script>

<style scoped>
.toast{
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  z-index: 999;
  background:rgba(0,0,0,0.6);
  color: #fff;
  padding: 10px;
}
</style>