import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    foodsList:[],
    item:{},
    list:[],
    isShow:false,
    history:[],
    order:[],
    orderItem:{},
    user:{
    },
    avatar:require('../assets/img/profile/profile.svg')
  },
  mutations: {
    //店铺信息
    getFoods(state,payload){
      state.foodsList.push(...payload)
    },
    //获取特定店铺信息
    getItem(state,payload){
      state.item=state.foodsList.find(
          (x)=>{
            if(x.name===payload){
              return x;
            }
          }
      )
    },
    //将商品添加购物车
    getList(state,payload){
      let old=state.history.find((item)=>item==payload)
      if(!old){
        state.history.push(payload)
      }
      let item=state.foodsList.find((item)=>item.name==payload);
      if(item){
        state.list.push(item)
      }
      else {
        state.isShow=true;
      }
    },
    getName(state,payload){
      if(payload==''){
        state.list=[];
        state.isShow=false;
      }
    },
    //添加订单
    getOrderList(state,payload){
      state.order.push(payload)
    },
    //获取特定订单信息
    getOrderItem(state,payload){
      state.orderItem=state.order.find(
          (x)=>{
            if(x.name===payload){
              return x;
            }
          }
      )
      console.log(state.orderItem)
    },
    //获取用户信息
    getUser(state,payload){
      state.user=payload;
      console.log(state.user)
    },
    getAvatar(state,payload){
      state.avatar=payload;
    },
  },
  actions: {
  },
  modules: {
  }
})
