<template>
<div class="cart-view-item">
  <div class="text">{{item.name}}</div>
  <div class="button">
    ￥{{item.price}}
    <img src="~assets/img/common/reduce.svg" alt=""  @click="reduce">
    <span>{{item.count}}</span>
    <img src="~assets/img/common/increase.svg" alt="" @click="increase">
  </div>
</div>
</template>

<script>
export default {
  name: "CartViewItem",
  props:{
    item:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  methods:{
    reduce(){
      this.$emit('reduce',this.item)
    },
    increase(){
      this.$emit('increase',this.item)
    }
  }
}
</script>

<style scoped>
.cart-view-item{
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
  border-bottom: 2px solid #eeeeee;
}
.button img{
  width: 24px;
  height: 24px;
  position: relative;
  top:5px;
}
.text{
  flex: 1;
  overflow: hidden;
  margin-left: 5px;
}
.button{
  flex: 1;
  text-align: right;
  margin-right: 5px;
}
</style>