<template>
  <swiper class="swiper" :options="swiperOption">
    <swiper-slide>
      <a>
        <img src="~assets/img/common/aa1.jpg" class="slider-img" @load="imgLoad">
      </a>
    </swiper-slide>
    <swiper-slide>
      <a> <img src="~assets/img/common/swiper2.jpg" class="slider-img" @load="imgLoad"></a>
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
    <!--    <div class="swiper-button-prev" slot="button-prev"></div>-->
    <!--    <div class="swiper-button-next" slot="button-next"></div>-->
  </swiper>
</template>

<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
export default {
  name: "SwiperView",
  components:{
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      swiperOption: {
        loop: true,
        initialSlide: 0, // 设定初始化时slide的索引，默认为0
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: {
          delay: 2500,
          disableOnInteraction: false,
        },
        pagination: {
          el: ".swiper-pagination",
          dynamicBullets: true,
        },
        // navigation: {
        //   nextEl: ".swiper-button-next",
        //   prevEl: ".swiper-button-prev",
        // },
      }
    }
  },
  methods:{
    imgLoad(){
      this.$bus.$emit('imgLoad')
    }
  }
}
</script>

<style scoped>
.swiper{
  width: 100%;
}
.slider-img{
  width: 100%;
}
</style>