  </div>
</template>

<script>
export default {
  name: "OrderTitle",
  props:{
    orderItem:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  methods:{
    rightClick() {
      this.$emit('rightClick')
    }
  }
}
</script>

