}
.order-name{
  position: relative;
  bottom:5px;
  font-size: 15px;
}
.order-image{
  margin-right: 15px;
}
.order-image img{
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background-color: #ff8198;
}
