<template>
<div class="order-list-item">
  <order-title :order-item="orderItem" @rightClick="rightClick">
    <div slot="right" class="text">
      <div>外卖专送</div>
    </div>
  </order-title>
  <div class="order-item-price" @click="orderItemClick">
    <order-scroll :order-item="orderItem"></order-scroll>
    <div class="total-price">
      ￥{{orderItem.totalPrice}}
      共{{orderItem.totalCount}}件
    </div>
  </div>
  <main-bottom></main-bottom>
</div>
</template>

<script>
import MainBottom from "./MainBottom";
import Scroll from "../../../components/common/scroll/Scroll";
import BScroll from "better-scroll";
import OrderScroll from "./OrderScroll";
import OrderTitle from "../../orderDeatil/childComps/OrderTitle";
export default {
  name: "OrderListItem",
  components: {OrderTitle, OrderScroll, Scroll, MainBottom},
  props: {
    orderItem: {
      type: Object,
      default() {
        return {}
      }
    },
  },
  methods:{
    rightClick(){
      this.$router.push('/detail/'+this.orderItem.name)
    },
    orderItemClick(){
      this.$router.push('/orderDetail/'+this.orderItem.name)
      this.$store.commit('getOrderItem',this.orderItem.name)
    }
  }
}
</script>

<style scoped>
.order-list-item{
  border-bottom: 10px solid #eeeeee;
  background-color: #ffffff;
}

.total-price{
  z-index: 9999;
  height: 100px;
  width: 60px;
  padding-top: 30px;
  text-align: center;
  border: 1px solid #ffffff;
  color: black;
  font-weight: bolder;
  font-family: Arial;
}
.order-item-price{
  display: flex;
}
.text{
  width: 80px;
  height: 26px;
  line-height: 26px;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  position: fixed;
  right: 0;
  margin-right: 10px;
  /*background: linear-gradient(91deg,#f1eefc,#9dc6ff 70%,#a5bcff);*/
  background: linear-gradient(135deg,#56c8ff,#6f99fc) no-repeat;
}
</style>