<template>
<div>
  <foods-list>
    <foods-list-item
        v-for="(item,index) in foodsList"
        :key="index"
        @click.native="itemClick(item)"
    >
      <div slot="left">
        <img v-lazy="item.image" alt="" @load="imgLoad">
        <div class="total-count" v-show="item.isActive">
          <Count :count="item.totalCount"></Count>
        </div>
      </div>
      <div slot="center" class="item-text">
        <span>{{item.name}}</span>
        <div class="count">
          <collect :collected="item.collected" :totalCollect="item.totalCollect"></collect>
          月售 {{item.count}}
        </div>
        <div>起送价￥{{item.price}} 免配送费</div>
      </div>
      <div slot="right" class="right">{{item.minutes}}分钟 {{item.distance}}km</div>
    </foods-list-item>
  </foods-list>
</div>
</template>

<script>
import FoodsList from "../../common/foodsList/FoodsList";
import FoodsListItem from "../../common/foodsList/FoodsListItem";
import Collect from "../../common/collect/Collect";
import Count from "../../common/count/Count";
export default {
  name: "MainFoodsList",
  components: {FoodsListItem, FoodsList,Collect,Count},
  props:{
    foodsList:{
      type:Array,
      default(){
        return []
      }
    }
  },
  methods:{
    //监听图片加载
    imgLoad(){
      // this.$emit('imgLoad');
      this.$bus.$emit('imgLoad');
    },
    itemClick(item){
      this.$store.commit('getItem',item.name)
      this.$router.push('/detail/'+item.name);
    }
  },
}
</script>

<style scoped>
.count{
  display: flex;
  margin-top:5px;
  margin-bottom: 5px;
}
.item-text span{
  color: 	#111;
}
.right{
  position: relative;
  top:30px;
}
.total-count{
  position: relative;
  bottom: 70px;
  left: 30px;
}
</style>