<template>
<div class="tab-control">
  <div v-for="(item,index) in tabs"
       :key="index"
       class="tab-control-item"
       @click="tabClick(index)"
       :class="{active:currentIndex===index}"
  >
    {{item}}
  </div>
</div>
</template>

<script>
export default {
  name: "TabControl",
  data(){
    return{
      tabs:['会员红包','离我最近','进店领券','立减优惠'],
      currentIndex:0
    }
  },
  methods:{
    tabClick(index){
      this.currentIndex=index;
    }
  }
}
</script>

<style scoped>
.tab-control{
  display: flex;
  font-size: 14px;
}
.tab-control-item{
  flex: 1;
  width: 80px;
  border: 1px solid #eeeeee;
  /*background: linear-gradient(135deg,#56c8ff,#6f99fc) no-repeat;*/
  margin: 10px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.active{
  background: linear-gradient(135deg,#56c8ff,#6f99fc) no-repeat;
  color: #ffffff;
}
</style>