<template>
<div class="detail-list">
  <div v-for="(item1,index1) in detail.list" ref="item">
    <div class="title">{{item1.name}}</div>
    <foods-list>
      <foods-list-item v-for="(item,index) in item1.detailList">
        <div slot="left">
          <img :src="item.image" alt="" @load="imgLoad">
        </div>
        <div slot="center" class="center">
          <div class="name"><span>{{item.name}}</span></div>
          <div class="sell"><span>已售{{item.sellCount}} 赞{{item.praise}}</span></div>
          <div class="price"><span>￥{{item.price}}/份</span></div>
        </div>
        <div slot="right" class="right" @click="addCart(item1,index1,item)">
          <img src="~assets/img/common/add.svg" alt="">
        </div>
      </foods-list-item>
    </foods-list>
  </div>
</div>
</template>

<script>
import FoodsList from "../../../components/common/foodsList/FoodsList";
import FoodsListItem from "../../../components/common/foodsList/FoodsListItem";
export default {
  name: "DetailList",
  components: {FoodsListItem, FoodsList},
  data(){
    return{
      currentIndex:0,
    }
  },
  computed:{
    currentType(){
      return this.detail.slideList[this.currentIndex];
    }
  },
  methods:{
    imgLoad(){
      this.$bus.$emit('imgLoad')
    },
    addCart(item1,index1,item){
      this.$emit('addCart',item1,index1,item)
    }
  },
  props:{
   detail:{
     type:Object,
     default(){
       return {}
     }
   }
  },
  activated() {
    console.log('activated')
    this.detail.topsY=[]
    this.$nextTick(()=>{
      this.detail.topsY.push(0)

      let totalHeight=0;
      for(let i=0;i<this.detail.list.length-1;i++){
        totalHeight+=this.$refs.item[i].offsetHeight
        this.detail.topsY.push(totalHeight)
      }
    })
    console.log(this.detail.topsY)
  }
}
</script>

<style scoped>
.title{
  width: 100%;
  font-size: 16px;
  background-color: #eeeeee;
  padding-left: 10px;
  border-left: 5px solid #222;
  border-bottom: 1px solid #eeeeee;
}
.center .name span{
  font-size: 16px;
  color: #666666;
}
.sell span{
  font-size: 10px;
}
.price{
  margin-top: 10px;
}
.price span{
  /*color:#1E90FF;*/
  color: red;
  font-size: 16px;
}
.right{
 position: relative;
 top:30px;
}
.right img{
  width: 30px;
  height: 30px;
}
</style>