<template>
<div class="detail-tab-control">
  <div v-for="(item,index) in tabs"
       class="tab-item"
       :class="{active:currentIndex===index}"
       @click="tabClick(index)"
  >
    <span>{{item}}</span>
  </div>
</div>
</template>

<script>
export default {
  name: "DetailTabControl",
  data(){
    return{
      tabs:['商品','评价','商家'],
      currentIndex:0,
    }
  },
  methods:{
    tabClick(index){
      this.currentIndex=index;
    }
  }
}
</script>

<style scoped>
.detail-tab-control{
  display: flex;
  /*padding: 10px;*/
  text-align: center;
  height: 40px;
  line-height: 40px;
  border-bottom: 2px solid #eeeeee;
}
.tab-item{
  flex: 1;
}
.active span{
  border-bottom: 3px solid #6495ED;
  padding-bottom: 5px;
}
</style>