<template>
    <div class="input">
      <input
          placeholder="请输入商户或商品名称"
          @keyup.enter="search"
          v-model="commodityName"
          @input="input"
      ></input>
    </div>
</template>

<script>
export default {
  name: "InputBox",
  data(){
    return{
      commodityName:'',
    }
  },
  methods:{
    search(){
      this.$store.commit('getList',this.commodityName)
      this.$router.push('/result/'+this.commodityName)
    },
    input(){
      this.$store.commit('getName',this.commodityName)
    },
  }
}
</script>

<style scoped>
.input input{
  width: 100%;
  padding-left: 30px;
  background:url('../../../assets/img/common/search.svg') no-repeat 10px center/15px;
  text-align: initial;
  display: inline-block;
  height: 30px;
  line-height: 15px;
  border-radius: 10px;
  /*除去input框默认样式*/
  /*去除阴影*/
  box-shadow:none;
  /*聚焦input的蓝色边框*/
  outline: none;
  /*textarea 禁止拖拽*/
  resize: none;
  /*去除边框*/
  border: none;
  /*常用于IOS下移除原生样式*/
  -webkit-appearance: none;
  /*点击高亮的颜色*/
  -webkit-tap-highlight-color:rgba(0,0,0,0);
}
</style>