<template>
<div>
  <user-info-list>
    <div slot="left">
      头像
    </div>
    <div slot="right">
      <div class="person">
        <div  @click.stop="uploadHeadImg">
          <img :src="avatar" alt=""/>
        </div>
        <input type="file" accept="image/*" @change="handleFile" class="hiddenInput"/>
      </div>
    </div>
  </user-info-list>
  <user-info-list>
    <div slot="left">
     用户名
    </div>
    <div slot="right">
      {{user.username}}
    </div>
  </user-info-list>
  <user-info-list>
    <div slot="left">
     账户密码
    </div>
    <div slot="right">
      {{user.password}}
    </div>
  </user-info-list>
  <user-info-list>
    <div slot="left">
     手机号
    </div>
    <div slot="right">
      <div>{{user.phone}}</div>
    </div>
  </user-info-list>
  <user-info-list>
    <div slot="left">
      微信账号
    </div>
    <div slot="right">
      未绑定微信账号
    </div>
  </user-info-list>
  <user-info-list>
    <div slot="left">
     注销账号
    </div>
    <div slot="right">
     注销后无法恢复，请谨慎操作
    </div>
  </user-info-list>
</div>
</template>

<script>
import UserInfoList from "./UserInfoList";
export default {
  name: "MainUserInfo",
  components: {UserInfoList},
  data(){
    return{
      avatar: require('../../../assets/img/profile/profile.svg'),
    }
  },
  props:{
    user:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  methods:{
    // 打开图片上传
    uploadHeadImg: function () {
      this.$el.querySelector('.hiddenInput').click()
    },
    // 将头像显示
    handleFile: function (e) {
      let $target = e.target || e.srcElement
      let file = $target.files[0]
      var reader = new FileReader()
      reader.onload = (data) => {
        let res = data.target || data.srcElement
        this.avatar = res.result
        this.$store.commit('getAvatar',this.avatar)
      }
      reader.readAsDataURL(file)
    },
  }
}
</script>

<style scoped>
.hiddenInput{
  display: none;
}

</style>