<template>
<div class="cart-view">
  <div class="title">
    <div class="price">餐盒费0元</div>
    <div class="delete" @click="deleteCart">
      <img src="~assets/img/common/delete.svg" alt="">
      清空购物车
    </div>
  </div>
 <cart-view-item
     v-for="item in cartList"
     :item="item"
     @increase="increase"
     @reduce="reduce"
 >
 </cart-view-item>
</div>
</template>

<script>
import CartViewItem from "./CartViewItem";
import Scroll from "../../common/scroll/Scroll";
export default {
  name: "CartView",
  components: {Scroll, CartViewItem},
  props:{
    cartList:{
      type:Array,
      default(){
        return []
      }
    }
  },
  methods:{
    increase(item){
      this.$emit('increase',item)
    },
    reduce(item){
      this.$emit('reduce',item)
    },
    deleteCart(){
      this.$emit('deleteCart')
    }
  }
}
</script>

<style scoped>
.cart-view{
  width: 100%;
  background-color:#ffffff;
  z-index: 999;
}
.title{
  display: flex;
  background-color: #eeeeee;
  font-size: 14px;
  height: 20px;
  line-height: 20px;
}
.price{
  flex: 1;
  padding-left: 5px;
}
.delete{
  width: 120px;
  text-align: right;
  padding-right: 5px;
}
.delete img{
  width: 16px;
  height: 16px;
}
</style>