<template>
<div class="collect">
    <img  v-for="(item,index) in collects"  src="~assets/img/food/collect_active.svg" >
    <img v-for="(item,index) in collection"  src="~assets/img/food/collect.svg" >
</div>
</template>

<script>
export default {
  name: "Collect",
  props:{
    totalCollect:{
      type:Number,
    },
    collected:{
      type:Number,
    }
  },
  data(){
    return {
      collection:[],
      collects: [],
    }
  },
  created() {
    this.collection.length=this.totalCollect-this.collected;
    this.collects.length=this.collected;
  }
}
</script>

<style scoped>
.collect img{
  width: 15px;
  height: 15px;
}
</style>